import React,{useState} from 'react'

export default function Textform(props) {
  
  const handleOnChange=(event)=> {
          console.log("on change");
          setText(event.target.value);
      }
    const handleUpClick=()=>{
        console.log("uppercase was clicked"+  text);
        let newText=text.toUpperCase();
        setText(newText)
        props.showAlert("converted to uppercase","success")
    }

    const handleCopy =()=>{
      console.log("text coppied")
      let newText = document.getElementById("mybox");
      newText.select();
      // text.setSelectionRange(0,9999);
      navigator.clipboard.writeText(newText.value)
      props.showAlert("text coppied","success")
    }
    
    const handleLoClick=()=>{
        console.log("Lowercase was clicked"+  text);
    const newText=text.toLowerCase();
         setText(newText)
         props.showAlert("converted to lowercase","success")
    }
    const handleClearClick=()=>{
        console.log("Lowercase was clicked"+  text);
    const newText='';
         setText(newText)
         props.showAlert("text cleared","success")
    }

    const handleExtraSpace =()=>{
      let newText = text.split(/[ ]+/)
      setText(newText.join(" "))
      props.showAlert("Extra space removed","success")

    }

    const [text,setText]=useState('enter the text here')
  return (<>
      <div className="container"style={{color:props.mode==='light'?'dark':'light'}}>
      
      <h1  style={{color:props.mode==='light'?'black':'white'}}>text down below</h1>
<div className="mb-3">
  
  <textarea className="form-control"style={{background:props.mode==='dark'
?'grey':'white',color:props.mode==='light'?'black':'white'}} value={text} onChange={handleOnChange} id="mybox" rows="8"></textarea>
</div>
<button className="btn btn-primary mx-2 my-2" onClick={handleUpClick}>convert to uppercase</button>

<button className="btn btn-primary mx-2 my-2" onClick={handleLoClick}>convert to Lowercase</button>

<button className="btn btn-primary mx-2 my-2" onClick={handleClearClick}>clear text</button>

<button className="btn btn-primary mx-2 my-2" onClick={handleCopy}>copy</button>

<button className="btn btn-primary mx-2 my-2" onClick={handleExtraSpace}>remove extra spaces</button>

</div>
<div className="container my-3"style={{color:props.mode==='light'?'black':'white'}}>
<h1>your text summary</h1>
<p>{text.split(" ").length}words and {text.length} characters</p>
<p>{0.008 * text.split(" ").length} minutes read </p>

<h2>preview</h2>
<p>{text}</p>
</div>

</>
  )
}
 